import React from 'react'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'
import {useDispatch, useSelector} from "react-redux";
import {AppStoreType} from "./bll/store";
import {initialStateType, loadingAC} from "./bll/loadingReducer";
import css from './HW10.module.css'
import loaderImg from './../../assets/loader/loader.gif'

function HW10() {
    // useSelector, useDispatch

    const state = useSelector<AppStoreType, initialStateType>(state => state.loading)
    const dispatch = useDispatch()
    const loading = state.loading

    const setLoading = () => {
        dispatch(loadingAC(true))
        setTimeout(() => {
            dispatch(loadingAC(false))
        }, 2000)
        // dispatch
        // setTimeout
        console.log('loading...')
    }

    return (
        <div>
            <hr/>
            homeworks 10

            {/*should work (должно работать)*/}
            {loading
                ? (
                    <div><img src={loaderImg} className={css.loader_img} alt='loading'/></div>
                ) : (
                    <div>
                        <SuperButton onClick={setLoading}>set loading...</SuperButton>
                    </div>
                )
            }

            <hr/>
            {/*для личного творчества, могу проверить*/}
            {/*<Alternative/>*/}
            <hr/>
        </div>
    )
}

export default HW10
